import React, { Component } from "react";
const numeral = require("numeral");
const uuid = require("uuid/v4");

export default class Rocket extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      rocket: null
    };
  }

  componentDidMount() {
    window.scrollTo(0, 0);
    let rocketId = this.props.match.params.id;
    const url = `https://api.spacexdata.com/v3/rockets/${rocketId}`;

    fetch(url)
      .then(res => res.json())
      .then(rocket => {
        this.setState({
          loading: false,
          rocket: rocket
        });
      });
  }
  render() {
    const rocket = this.state.rocket;

    console.log(rocket);

    return (
      <div>
        {this.state.loading || !this.state.rocket ? (
          <p>Loading...</p>
        ) : (
          <div
            className="card"
            style={{
              height: "fit-content",
              maxWidth: "800px",
              position: "relative",
              margin: "0 auto"
            }}
          >
            <div
              className=" pb-0"
              style={{ padding: "20px", backgroundColor: "#000" }}
            >
              <h2 className=" text-primary">
                {rocket.rocket_name}

                <span
                  className={`badge float-right ${
                    rocket.active ? "badge-success" : "badge-warning"
                  }`}
                  style={{ fontSize: "20px" }}
                >
                  {rocket.active ? "active" : "not active"}
                </span>
              </h2>
            </div>
            <table
              className="table table-bordered text-center mb-0"
              style={{ backgroundColor: "#111" }}
            >
              <thead>
                <tr>
                  <th>First flight</th>
                  <th>stages</th>
                  <th>Cost per Launch</th>
                  <th className="text-warning">Success rate</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>{rocket.first_flight}</td>
                  <td>{rocket.stages}</td>
                  <td>
                    {numeral(rocket.cost_per_launch).format("$ 0,0[.]00")}
                  </td>
                  <td
                    className={
                      rocket.success_rate_pct < 80
                        ? "text-danger"
                        : "text-success"
                    }
                  >
                    {rocket.success_rate_pct}%
                  </td>
                </tr>
              </tbody>
            </table>
            <div
              style={{
                backgroundColor: "#111",
                overflow: "hidden",
                textAlign: "center",
                position: "relative",
                height: "400px"
              }}
            >
              <img
                samesite="None"
                alt="rocket"
                style={{
                  height: "400px",
                  width: "auto",
                  position: "absolute",
                  left: "50%",
                  top: "50%",
                  transform: "translateY(-50%) translateX(-50%)"
                }}
                src={rocket.flickr_images}
              />
            </div>
            <div className="card-body">
              <p>{rocket.description}</p>
            </div>

            <div
              className="d-flex flex-wrap justify-content-center"
              style={{
                alignItems: "center",
                backgroundColor: "#000",
                padding: "20px 0"
              }}
            >
              {rocket.flickr_images.map(image => {
                return (
                  <div key={uuid()} style={{ width: "50%" }}>
                    <a href={image} target="_blank" rel="noopener noreferrer">
                      <img src={image} alt="rocket" style={{ width: "100%" }} />
                    </a>
                  </div>
                );
              })}
            </div>
            <br />
            <div id="accordion">
              <div className="card">
                <div
                  className="card-header pb-0"
                  style={{ backgroundColor: "#111" }}
                >
                  <button
                    className="btn btn-link"
                    data-toggle="collapse"
                    data-target="#payload"
                    aria-expanded="true"
                    aria-controls="#payload"
                    style={{ width: "100%" }}
                  >
                    <h5>Payload weights</h5>
                  </button>
                </div>

                <div id="payload" className="collapse" data-parent="#accordion">
                  <table
                    className="table table-bordered text-center mb-0"
                    style={{ backgroundColor: "#111" }}
                  >
                    <thead>
                      <tr>
                        <th>Name</th>
                        <th>kg</th>
                        <th>lb</th>
                      </tr>
                    </thead>
                    <tbody>
                      {rocket.payload_weights.map(load => {
                        return (
                          <tr key={uuid()}>
                            <td>{load.name}</td>
                            <td>{load.kg}</td>
                            <td>{load.lb}</td>
                          </tr>
                        );
                      })}
                    </tbody>
                  </table>
                </div>
              </div>

              <div className="card">
                <div
                  className="card-header pb-0"
                  style={{ backgroundColor: "#111" }}
                >
                  <button
                    className="btn btn-link"
                    data-toggle="collapse"
                    data-target="#engine"
                    aria-expanded="true"
                    aria-controls="#engine"
                    style={{ width: "100%" }}
                  >
                    <h5>Engines</h5>
                  </button>
                </div>

                <div id="engine" className="collapse" data-parent="#accordion">
                  <table
                    className="table table-bordered text-center mb-0"
                    style={{ backgroundColor: "#111" }}
                  >
                    <tbody>
                      <tr>
                        <th>Type</th>
                        <td>{rocket.engines.type}</td>
                      </tr>
                      <tr>
                        <th>Version</th>
                        <td>{rocket.engines.version}</td>
                      </tr>
                      <tr>
                        <th>Number</th>
                        <td>{rocket.engines.number}</td>
                      </tr>
                      <tr>
                        <th>Layout</th>
                        <td>
                          {rocket.engines.layout
                            ? rocket.engines.layout
                            : "n/a"}
                        </td>
                      </tr>
                      <tr>
                        <th>Propellant 1</th>
                        <td>{rocket.engines.propellant_1}</td>
                      </tr>
                      <tr>
                        <th>Propellant 2</th>
                        <td>{rocket.engines.propellant_2}</td>
                      </tr>
                      <tr>
                        <th>Thrust sea level</th>
                        <td>
                          {rocket.engines.thrust_sea_level.kN}kN <br />{" "}
                          {rocket.engines.thrust_sea_level.lbf}lbf
                        </td>
                      </tr>
                      <tr>
                        <th>Thrust vacuum</th>
                        <td>
                          {rocket.engines.thrust_vacuum.kN}kN <br />{" "}
                          {rocket.engines.thrust_vacuum.lbf}lbf
                        </td>
                      </tr>
                      <tr>
                        <th>Thrust to weight</th>
                        <td>{rocket.engines.thrust_to_weight}</td>
                      </tr>
                      <tr>
                        <th>Engine loss max</th>
                        <td>{rocket.engines.engine_loss_max}</td>
                      </tr>

                      <tr>
                        <th>ISP</th>
                        <td>
                          <table className="table">
                            <thead>
                              <tr>
                                <th>Sea level</th>
                                <th>Vacuum</th>
                              </tr>
                            </thead>
                            <tbody>
                              <tr>
                                <td>{rocket.engines.isp.sea_level}</td>
                                <td>{rocket.engines.isp.vacuum}</td>
                              </tr>
                            </tbody>
                          </table>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>

            <div className="card">
              <div
                className="card-header pb-0"
                style={{ backgroundColor: "#111" }}
              >
                <button
                  className="btn btn-link"
                  data-toggle="collapse"
                  data-target="#first-stage"
                  aria-expanded="true"
                  aria-controls="#first-stage"
                  style={{ width: "100%" }}
                >
                  <h5>First Stage</h5>
                </button>
              </div>

              <div
                id="first-stage"
                className="collapse"
                data-parent="#accordion"
              >
                <table
                  className="table table-bordered text-center mb-0"
                  style={{ backgroundColor: "#111" }}
                >
                  <tbody>
                    <tr>
                      <th>Reusable</th>
                      <td>
                        {rocket.first_stage.burn_time_sec ? "True" : "false"}
                      </td>
                    </tr>
                    <tr>
                      <th>Engines</th>
                      <td>{rocket.first_stage.engines}</td>
                    </tr>
                    <tr>
                      <th>Burn time</th>
                      <td>{rocket.first_stage.burn_time_sec}s</td>
                    </tr>
                    <tr>
                      <th>Fuel amount</th>
                      <td>{rocket.first_stage.fuel_amount_tons} Tons</td>
                    </tr>

                    <tr>
                      <th>Thrust sea level</th>
                      <td>
                        {rocket.first_stage.thrust_sea_level.kN} kN <br />
                        {rocket.first_stage.thrust_sea_level.lbf} lbf
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
            <div className="card">
              <div
                className="card-header pb-0"
                style={{ backgroundColor: "#111" }}
              >
                <button
                  className="btn btn-link"
                  data-toggle="collapse"
                  data-target="#second-engine"
                  aria-expanded="true"
                  aria-controls="#second-engine"
                  style={{ width: "100%" }}
                >
                  <h5>Second Stage</h5>
                </button>
              </div>

              <div
                id="second-engine"
                className="collapse"
                data-parent="#accordion"
              >
                <table
                  className="table table-bordered text-center mb-0"
                  style={{ backgroundColor: "#111" }}
                >
                  <tbody>
                    <tr>
                      <th>Reusable</th>
                      <td>
                        {rocket.second_stage.burn_time_sec ? "True" : "false"}
                      </td>
                    </tr>
                    <tr>
                      <th>Engines</th>
                      <td>{rocket.second_stage.engines}</td>
                    </tr>
                    <tr>
                      <th>Burn time</th>
                      <td>{rocket.second_stage.burn_time_sec}s</td>
                    </tr>
                    <tr>
                      <th>Fuel amount</th>
                      <td>{rocket.second_stage.fuel_amount_tons} Tons</td>
                    </tr>
                    <tr>
                      <th>Thrust</th>
                      <td>
                        {rocket.second_stage.thrust.kN} kN <br />
                        {rocket.second_stage.thrust.lbf} lbf
                      </td>
                    </tr>
                    <tr>
                      <th>Payloads</th>
                      <td>
                        <table className="table">
                          <thead>
                            <tr>
                              <th>Option 1</th>
                              <th>Option 2</th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr>
                              <td>{rocket.second_stage.payloads.option_1}</td>
                              <td>
                                {rocket.second_stage.payloads.option_2
                                  ? rocket.second_stage.payloads.option_2
                                  : "n/a"}
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        )}
      </div>
    );
  }
}
