import React, { Component } from "react";
import { Link } from "react-router-dom";
const uuid = require("uuid/v4");

class Rockets extends Component {
  constructor(props) {
    super(props);
    this._isMounted = false;
    this.state = {
      rockets: [],
      loading: true
    };
  }

  componentDidMount() {
    this._isMounted = true;
    this._isMounted &&
      fetch("https://api.spacexdata.com/v3/rockets")
        .then(res => res.json())
        .then(data =>
          this.setState({
            rockets: data,
            rocketId: data.rocket_id,
            loading: false
          })
        );
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  render() {
    console.log(this.state.rockets);

    return (
      <>
        <h1 className="display-4 my-3">Rockets</h1>
        {this.state.loading || !this.state.rockets ? (
          <p>Loading...</p>
        ) : (
          <div className="d-flex flex-wrap justify-content-center">
            {this.state.rockets.map(rocket => {
              const {
                rocket_name,
                rocket_id,
                description,
                flickr_images,
                success_rate_pct
              } = rocket;
              return (
                <div
                  className="card m-3 pt-3"
                  key={uuid()}
                  style={{
                    width: "40%",
                    minWidth: "300px",
                    position: "relative"
                  }}
                >
                  <h3 className="">{rocket_name}</h3>
                  <p
                    style={{ position: "absolute", top: "10px", right: "10px" }}
                  >
                    {success_rate_pct > 0 ? (
                      <>
                        {" "}
                        Success rate{" "}
                        <span
                          className={
                            success_rate_pct >= 90
                              ? "text-success"
                              : "text-danger"
                          }
                        >
                          {" "}
                          {success_rate_pct}%
                        </span>
                      </>
                    ) : (
                      "No data"
                    )}
                  </p>

                  <div
                    style={{
                      backgroundColor: "#000",
                      padding: "7px 0",
                      overflow: "hidden"
                    }}
                  >
                    <img
                      alt="rocket"
                      style={{
                        height: "220px",
                        width: "auto",
                        display: "block",
                        margin: "0 auto"
                      }}
                      src={flickr_images}
                    />
                  </div>
                  <div className="card-body">
                    <p className="card-text">{description}</p>
                  </div>
                  <div className="card-body">
                    <Link
                      to={`/rockets/${rocket_id}`}
                      className="btn btn-secondary"
                    >
                      Rocket Details
                    </Link>
                  </div>
                </div>
              );
            })}
          </div>
        )}
      </>
    );
  }
}

export default Rockets;
