import React, { Component } from "react";
const uuid = require("uuid/v4");

export default class LandingPads extends Component {
  constructor(props) {
    super(props);
    this._isMounted = false;
    this.state = {
      landPads: [],
      loading: true
    };
  }

  componentDidMount() {
    this._isMounted = true;
    this._isMounted &&
      fetch("https://api.spacexdata.com/v3/landpads")
        .then(res => res.json())
        .then(data =>
          this.setState({
            landPads: data,
            loading: false
          })
        );
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  render() {
    console.log(this.state.landPads);

    return (
      <div>
        <h1>Landing Pads</h1>
        {this.state.loading || !this.state.landPads ? (
          <p>Loading...</p>
        ) : (
          <div className="row ">
            {this.state.landPads.map(pad => {
              return (
                <div className="col-md-6 mb-3 align-self-center" key={uuid()}>
                  <div className="card">
                    <div className="card-body">
                      <h5 className="card-title text-primary">
                        {pad.full_name}

                        <span
                          className={`badge float-right ${
                            pad.status === "active"
                              ? "badge-success"
                              : pad.status === "retired"
                              ? "badge-warning"
                              : pad.status === "under construction"
                              ? "badge-info"
                              : "badge-danger"
                          }`}
                        >
                          {pad.status}
                        </span>
                      </h5>
                      <p>
                        Location: {pad.location.name} <br />
                        Region: {pad.location.region}
                      </p>
                      <p>Landing type: {pad.landing_type}</p>
                      <p className="card-text">{pad.details}</p>
                    </div>

                    <table className="table table-bordered">
                      <tbody style={{ backgroundColor: "#111" }}>
                        <tr>
                          <th scope="row">Landing attempts</th>
                          <td>{pad.attempted_landings}</td>
                        </tr>
                        <tr>
                          <th scope="row">Successful landings</th>
                          <td>{pad.successful_landings}</td>
                        </tr>
                      </tbody>
                    </table>
                    <div className="card-body" style={{ textAlign: "right" }}>
                      Read more on{" "}
                      <a
                        href={pad.wikipedia}
                        target="_blank"
                        rel="noopener noreferrer"
                      >
                        Wikipedia
                      </a>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        )}
      </div>
    );
  }
}
