import React, { Component } from "react";
const uuid = require("uuid/v4");
const noImage = require("../noimg.jpg");
const numeral = require("numeral");

export default class Ships extends Component {
  constructor(props) {
    super(props);
    this._isMounted = false;
    this.state = {
      ships: [],
      loading: true
    };
  }

  componentDidMount() {
    this._isMounted = true;
    this._isMounted &&
      fetch("https://api.spacexdata.com/v3/ships")
        .then(res => res.json())
        .then(data =>
          this.setState({
            ships: data,
            loading: false
          })
        );
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  render() {
    console.log(this.state.ships);

    return (
      <>
        <h1 className="display-4 my-3">Ships</h1>
        {this.state.loading || !this.state.ships ? (
          <p>Loading...</p>
        ) : (
          <div className="d-flex flex-wrap justify-content-center">
            {this.state.ships.map(ship => {
              return (
                <div
                  className="card m-3 mb-5"
                  key={uuid()}
                  style={{
                    width: "46%",
                    height: "fit-content",
                    minWidth: "320px",
                    position: "relative"
                  }}
                >
                  <div
                    className=" pb-0"
                    style={{ padding: "20px", backgroundColor: "#000" }}
                  >
                    <h5 className="card-title text-primary">
                      {ship.ship_name}

                      <span
                        className={`badge float-right ${
                          ship.status ? "badge-success" : "badge-warning"
                        }`}
                      >
                        {ship.status ? "active" : "not active"}
                      </span>
                    </h5>
                  </div>
                  <table
                    className="table table-bordered text-center mb-0"
                    style={{ backgroundColor: "#111" }}
                  >
                    <thead className="text-warning">
                      <tr>
                        {ship.status && (
                          <th className="text-success">status</th>
                        )}
                        <th>Home Port</th>
                        <th>Ship Id</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        {ship.status && <th>{ship.status}</th>}
                        <td>{ship.home_port}</td>
                        <td>{ship.ship_id}</td>
                      </tr>
                    </tbody>
                  </table>
                  <div
                    style={{
                      backgroundColor: "#111",
                      overflow: "hidden",
                      textAlign: "center"
                    }}
                  >
                    <img
                      samesite="None"
                      alt="rocket"
                      style={{
                        height: "220px",
                        width: "auto"
                      }}
                      src={ship.image ? ship.image : noImage}
                    />
                  </div>

                  <div id="accordion">
                    <div className="card">
                      <div
                        className="card-header pb-0"
                        id="headingOne"
                        style={{ backgroundColor: "#111" }}
                      >
                        <button
                          className="btn btn-link"
                          data-toggle="collapse"
                          data-target={`#${ship.ship_id}missions`}
                          aria-expanded="true"
                          aria-controls={`#${ship.ship_id}missions`}
                          style={{ width: "100%" }}
                        >
                          <h5>
                            Missions
                            <span className={`badge float-right badge-primary`}>
                              {ship.missions.length}
                            </span>
                          </h5>
                        </button>
                      </div>

                      <div
                        id={ship.ship_id + "missions"}
                        className="collapse"
                        aria-labelledby="headingOne"
                        data-parent="#accordion"
                      >
                        <ul className="list-group">
                          {ship.missions.map(mission => (
                            <li
                              key={uuid()}
                              className="list-group-item d-flex justify-content-between align-items-center"
                              style={{ backgroundColor: "#111" }}
                            >
                              <strong>Name: </strong> {mission.name} <br />
                              <strong>flight: </strong>
                              <span className="badge badge-primary badge-pill">
                                {mission.flight}
                              </span>
                            </li>
                          ))}
                        </ul>
                      </div>
                    </div>

                    <div className="card">
                      <div
                        className="card-header pb-0"
                        style={{ backgroundColor: "#111" }}
                      >
                        <button
                          className="btn btn-link"
                          data-toggle="collapse"
                          data-target={`#${ship.ship_id}stats`}
                          aria-expanded="true"
                          aria-controls={`#${ship.ship_id}stats`}
                          style={{ width: "100%" }}
                        >
                          <h5>Statistics</h5>
                        </button>
                      </div>

                      <div
                        id={ship.ship_id + "stats"}
                        className="collapse"
                        data-parent="#accordion"
                      >
                        <table
                          className="table table-bordered text-center mb-0"
                          style={{ backgroundColor: "#111" }}
                        >
                          <tbody>
                            {ship.position.latitude && (
                              <tr>
                                <th className="text-success">position</th>
                                <td>
                                  {ship.position.latitude} lat <br />
                                  {ship.position.longitude} lon
                                </td>
                              </tr>
                            )}
                            {ship.course_deg !== 0 && ship.course_deg && (
                              <tr>
                                <th className="text-success">course</th>
                                <td>{ship.course_deg}&deg;</td>
                              </tr>
                            )}
                            {ship.ship_type && (
                              <tr>
                                <th>Type</th>
                                <td>{ship.ship_type}</td>
                              </tr>
                            )}
                            {ship.year_built && (
                              <tr>
                                <th>Build</th>
                                <td>{ship.year_built}</td>
                              </tr>
                            )}
                            {ship.ship_model && (
                              <tr>
                                <th>Model</th>
                                <td>{ship.ship_model}</td>
                              </tr>
                            )}
                            {ship.imo && (
                              <tr>
                                <th>IMO</th>
                                <td>{ship.imo}</td>
                              </tr>
                            )}
                            {ship.abs && (
                              <tr>
                                <th>ABS</th>
                                <td>{ship.abs}</td>
                              </tr>
                            )}
                            {ship.mmsi && (
                              <tr>
                                <th>MMSI</th>
                                <td>{ship.mmsi}</td>
                              </tr>
                            )}
                            {ship.weight_kg && (
                              <tr>
                                <th>Weight</th>
                                <td>
                                  {numeral(ship.weight_kg).format(0, 0)}kg /
                                  {numeral(ship.weight_lbs).format(0, 0)}lbs
                                </td>
                              </tr>
                            )}

                            {ship.speed_kn > 0 && ship.speed_kn && (
                              <tr>
                                <th>Speed</th>
                                <td>{ship.speed_kn}kn</td>
                              </tr>
                            )}
                            {ship.attempted_landings !== 0 &&
                              ship.attempted_landings && (
                                <tr>
                                  <th>Attempted landings</th>
                                  <td>{ship.attempted_landings}</td>
                                </tr>
                              )}
                            {ship.successful_landings !== 0 &&
                              ship.successful_landings && (
                                <tr>
                                  <th>Successful landings</th>
                                  <td>{ship.successful_landings}</td>
                                </tr>
                              )}
                            {ship.roles && (
                              <tr>
                                <th className="text-primary">Roles</th>
                                <td>
                                  {ship.roles.map(role => (
                                    <p key={uuid()}>{role}</p>
                                  ))}
                                </td>
                              </tr>
                            )}
                          </tbody>
                        </table>
                      </div>
                    </div>

                    {ship.url && (
                      <div
                        className="card-header pb-0 text-center"
                        style={{ backgroundColor: "#111" }}
                      >
                        Watch on
                        <a
                          href={ship.url}
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          <h5 className="text-warning">MarineTraffic</h5>{" "}
                        </a>
                      </div>
                    )}
                  </div>
                </div>
              );
            })}
          </div>
        )}
      </>
    );
  }
}
