import React from "react";
import { HashRouter as Router, Route } from "react-router-dom";
import Nav from "./components/Nav";
import Home from "./components/Home";
import Rockets from "./components/Rockets";
import Rocket from "./components/Rocket";
import Launches from "./components/Launches";
import UpcomingLaunches from "./components/UpcomingLaunches";
import PastLaunches from "./components/PastLaunches";
import Launch from "./components/Launch";
import Capsules from "./components/Capsules";
import Cores from "./components/Cores";
import Dragons from "./components/Dragons";
import LandingPads from "./components/LandingPads";
import LaunchPads from "./components/LaunchPads";
import Payloads from "./components/Payloads";
import Roadster from "./components/Roadster";
import Ships from "./components/Ships";
import "bootswatch/dist/cyborg/bootstrap.min.css";
import "./App.css";
// import logo from "./logo.png";

function App() {
  return (
    <Router forceRefresh={true}>
      <Nav />
      {/* <img src={logo} alt="logo" style={{ width: "100%" }} /> */}
      <div className="container-fluid" style={{ overflowX: "auto" }}>
        <Route exact path="/" component={Home} />
        <Route exact path="/launches" component={Launches} />
        <Route
          exact
          path="/launches/launch/:flight_number"
          component={Launch}
        />
        <Route path="/launches/upcoming" component={UpcomingLaunches} />
        <Route path="/launches/past" component={PastLaunches} />
        <Route exact path="/rockets" component={Rockets} />
        <Route exact path="/rockets/:id" component={Rocket} />
        <Route exact path="/capsules" component={Capsules} />
        <Route exact path="/cores" component={Cores} />
        <Route exact path="/dragons" component={Dragons} />
        <Route exact path="/landing-pads" component={LandingPads} />
        <Route exact path="/launch-pads" component={LaunchPads} />
        <Route exact path="/payloads" component={Payloads} />
        <Route exact path="/roadster" component={Roadster} />
        <Route exact path="/ships" component={Ships} />
      </div>
    </Router>
  );
}

export default App;
