import React, { Component } from "react";
const uuid = require("uuid/v4");

class Timeline extends Component {
  constructor(props) {
    super(props);
    this.state = {
      timeline: []
    };
  }
  componentDidMount() {
    let timeline = this.props.timeline;
    const sortTimeline = timeline => {
      let arr = [];
      let prop;
      for (prop in timeline) {
        if (timeline.hasOwnProperty(prop)) {
          arr.push({
            key: prop,
            value: timeline[prop]
          });
        }
      }
      arr.sort((a, b) => a.value - b.value);
      return arr;
    };
    this.setState({
      timeline: sortTimeline(timeline)
    });
  }

  render() {
    return (
      <>
        <h2>Timeline</h2>
        <br />
        <table className="table" style={{ maxWidth: "500px" }}>
          <thead>
            <tr>
              <th>#</th>
              <th>Time (T)</th>
              <th>Details</th>
            </tr>
          </thead>
          <tbody>
            {this.state.timeline.length !== 0 &&
              this.state.timeline.map((x, i) => (
                <tr key={uuid()}>
                  <td>{i}</td>
                  <td>
                    <span
                      className={
                        x.value < 0
                          ? "text-warning"
                          : x.value > 0
                          ? "text-primary"
                          : "text-success"
                      }
                    >
                      {x.value}
                    </span>
                    <span style={{ fontSize: "12px" }}>s</span>
                  </td>
                  <td>{x.key.replace(/_/g, " ")}</td>
                </tr>
              ))}
          </tbody>
        </table>
      </>
    );
  }
}

export default Timeline;
