import React, { Component } from "react";
const uuid = require("uuid/v4");

export default class LaunchPads extends Component {
  constructor(props) {
    super(props);
    this._isMounted = false;
    this.state = {
      launchPads: [],
      loading: true
    };
  }

  componentDidMount() {
    this._isMounted = true;
    this._isMounted &&
      fetch("https://api.spacexdata.com/v3/launchpads")
        .then(res => res.json())
        .then(data =>
          this.setState({
            launchPads: data,
            loading: false
          })
        );
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  render() {
    console.log(this.state.launchPads);

    return (
      <div>
        <h1>Launch Pads</h1>
        {this.state.loading || !this.state.launchPads ? (
          <p>Loading...</p>
        ) : (
          <div className="row ">
            {this.state.launchPads.map(pad => {
              return (
                <div className="col-md-6 mb-3 align-self-center" key={uuid()}>
                  <div className="card">
                    <div className="card-body">
                      <h5 className="card-title text-primary">
                        {pad.name}

                        <span
                          className={`badge float-right ${
                            pad.status === "active"
                              ? "badge-success"
                              : pad.status === "retired"
                              ? "badge-warning"
                              : pad.status === "under construction"
                              ? "badge-info"
                              : "badge-danger"
                          }`}
                        >
                          {pad.status}
                        </span>
                      </h5>
                      <p>
                        Location: {pad.location.name} <br />
                        Region: {pad.location.region}
                      </p>
                      <p className="card-text">{pad.details}</p>
                    </div>

                    <table className="table table-bordered">
                      <tbody style={{ backgroundColor: "#111" }}>
                        <tr>
                          <th scope="row">Launch attempts</th>
                          <td>{pad.attempted_launches}</td>
                        </tr>
                        <tr>
                          <th scope="row">Successful launches</th>
                          <td>{pad.successful_launches}</td>
                        </tr>
                        <tr>
                          <th scope="row" className="text-primary">
                            Vehicles launched:
                          </th>
                          <td>
                            {pad.vehicles_launched.map(vehicle => {
                              return <p key={uuid()}>{vehicle}</p>;
                            })}
                          </td>
                        </tr>
                      </tbody>
                    </table>
                    <div className="card-body" style={{ textAlign: "right" }}>
                      Read more on{" "}
                      <a
                        href={pad.wikipedia}
                        target="_blank"
                        rel="noopener noreferrer"
                      >
                        Wikipedia
                      </a>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        )}
      </div>
    );
  }
}
