import React, { Component } from "react";
import Moment from "react-moment";
const uuid = require("uuid/v4");

export default class Cores extends Component {
  constructor(props) {
    super(props);
    this._isMounted = false;
    this.state = {
      cores: [],
      loading: true
    };
  }

  componentDidMount() {
    this._isMounted = true;
    this._isMounted &&
      fetch("https://api.spacexdata.com/v3/cores")
        .then(res => res.json())
        .then(data =>
          this.setState({
            cores: data,
            loading: false
          })
        );
  }

  componentWillUnmount() {
    this._isMounted = false;
    this.loading = true;
  }

  render() {
    console.log(this.state.cores);

    return (
      <div>
        <h1>Cores</h1>
        {this.state.loading || !this.state.cores ? (
          <p>Loading...</p>
        ) : (
          <>
            <div
              className="d-flex flex-wrap"
              style={{ justifyContent: "center" }}
            >
              {this.state.cores.map(core => {
                return (
                  <div
                    className="card"
                    style={{ width: "20rem", margin: "1rem" }}
                    key={uuid()}
                  >
                    <div
                      className="card-header"
                      style={{ backgroundColor: "#111" }}
                    >
                      <strong>Block: </strong> {core.block}
                    </div>
                    <div className="card-body">
                      <h4 className="card-title">
                        {" "}
                        <span className="text-primary">{core.core_serial}</span>
                        <span
                          className={`badge ${
                            core.status === "active"
                              ? "badge-success"
                              : core.status === "lost"
                              ? "badge-danger"
                              : core.status === "inactive"
                              ? "badge-info"
                              : null
                          } float-right`}
                        >
                          {core.status}
                        </span>
                      </h4>
                      {core.original_launch && (
                        <p>
                          {" "}
                          <strong>Original launch:</strong>{" "}
                          <Moment format="l">{core.original_launch}</Moment>
                        </p>
                      )}
                      <hr />
                      <table className="table table-bordered">
                        <tbody>
                          <tr style={{ backgroundColor: "#000" }}>
                            <th
                              scope="col"
                              title="rtls = Return To Landing Site"
                            >
                              Ground landings (rtls)
                            </th>
                            <td>{core.rtls_landings}</td>
                          </tr>
                          <tr style={{ backgroundColor: "#111" }}>
                            <th scope="col">rtls Attempts</th>
                            <td>{core.rtls_attempts}</td>
                          </tr>
                          <tr style={{ backgroundColor: "#000" }}>
                            <th scope="col">reused</th>
                            <td>{core.reuse_count}</td>
                          </tr>
                          <tr style={{ backgroundColor: "#111" }}>
                            <th scope="col">water Landing</th>
                            <td>{core.water_landing ? "Yes" : "No"}</td>
                          </tr>
                        </tbody>
                      </table>
                      <div className="card mb-3" style={{ maxWidth: "20rem" }}>
                        <div
                          className="card-header"
                          style={{ backgroundColor: "#000" }}
                        >
                          Mission{core.missions.length > 1 ? "s" : null}:
                        </div>

                        <ul className="list-group">
                          {core.missions.map(mission => (
                            <li
                              key={uuid()}
                              className="list-group-item d-flex justify-content-between align-items-center"
                              style={{ backgroundColor: "#111" }}
                            >
                              <strong>Name: </strong> {mission.name} <br />
                              <strong>flight: </strong>
                              <span className="badge badge-primary badge-pill">
                                {mission.flight}
                              </span>
                            </li>
                          ))}
                        </ul>
                      </div>

                      {core.details && (
                        <div className="card mb-3">
                          <div
                            className="card-header"
                            style={{ backgroundColor: "#000" }}
                          >
                            Details
                          </div>
                          <div
                            className="card-body"
                            style={{ backgroundColor: "#111" }}
                          >
                            <p className="card-text">{core.details}</p>
                          </div>
                        </div>
                      )}
                    </div>
                  </div>
                );
              })}
            </div>
          </>
        )}
      </div>
    );
  }
}
