import React, { Component } from "react";
const uuid = require("uuid/v4");

export default class Payloads extends Component {
  constructor(props) {
    super(props);
    this._isMounted = false;
    this.state = {
      payLoads: [],
      loading: true
    };
  }

  componentDidMount() {
    this._isMounted = true;
    this._isMounted &&
      fetch("https://api.spacexdata.com/v3/payloads")
        .then(res => res.json())
        .then(data =>
          this.setState({
            payLoads: data,
            loading: false
          })
        );
  }
  componentWillUnmount() {
    this._isMounted = false;
  }

  render() {
    console.log(this.state.payLoads);

    return (
      <div>
        <h1>Payloads</h1>
        {this.state.loading || !this.state.payLoads ? (
          <p>Loading...</p>
        ) : (
          <div className="d-flex flex-wrap justify-content-center">
            {this.state.payLoads.map(load => {
              return (
                <div
                  className="card m-1"
                  style={{ minWidth: "300px", width: "32%" }}
                  key={uuid()}
                >
                  <h4 className="text-center m-1 text-primary">
                    {load.payload_id}
                  </h4>

                  <table
                    className="table table-bordered"
                    style={{ width: "100%", tableLayout: "fixed" }}
                  >
                    <tbody style={{ backgroundColor: "#111" }}>
                      <tr>
                        <th scope="row">Manufacturer</th>
                        {load.manufacturer ? (
                          <td>{load.manufacturer}</td>
                        ) : (
                          <td>no data</td>
                        )}
                      </tr>
                      <tr>
                        <th scope="row">Payload type</th>
                        <td>{load.payload_type}</td>
                      </tr>
                      <tr>
                        <th scope="row">Payload mass</th>
                        {load.payload_mass_kg ? (
                          <td>
                            {load.payload_mass_kg}kg / {load.payload_mass_lbs}
                            lbs{" "}
                          </td>
                        ) : (
                          <td>no data</td>
                        )}
                      </tr>
                      <tr>
                        <th scope="row">reused</th>
                        <td
                          className={
                            load.reused ? "text-success" : "text-danger"
                          }
                        >
                          {load.reused ? "Yes" : "No"}
                        </td>
                      </tr>
                      <tr>
                        <th scope="row">Orbit</th>
                        <td>{load.orbit}</td>
                      </tr>
                      <tr>
                        <th scope="row" className="text-primary">
                          Customers:
                        </th>
                        <td>
                          {load.customers.map(customer => {
                            return <p key={uuid()}>{customer}</p>;
                          })}
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              );
            })}
          </div>
        )}
      </div>
    );
  }
}
