import React, { Component } from "react";
import Moment from "react-moment";
import Countdown from "react-countdown-now";
import CountUp from "react-countup";
import { Link } from "react-router-dom";

const uuid = require("uuid/v4");

export class Home extends Component {
  constructor(props) {
    super(props);
    this._isMounted = false;
    this.state = {
      next: null,
      latest: null,
      rockets: null,
      roadster: null,
      info: null,
      totalflight: 0,
      loading: true
    };
  }

  async componentDidMount() {
    this._isMounted = true;
    fetch("https://api.spacexdata.com/v3/launches/next")
      .then(res => res.json())
      .then(data => this.setState({ next: data }));

    fetch("https://api.spacexdata.com/v3/launches/latest")
      .then(res => res.json())
      .then(data => this.setState({ latest: data }));

    fetch("https://api.spacexdata.com/v3/rockets")
      .then(res => res.json())
      .then(data => this.setState({ rockets: data }));

    fetch("https://api.spacexdata.com/v3/roadster")
      .then(res => res.json())
      .then(data => this.setState({ roadster: data }));

    fetch("https://api.spacexdata.com/v3/info")
      .then(res => res.json())
      .then(data => this.setState({ info: data }));

    fetch("https://api.spacexdata.com/v3/launches/past")
      .then(res => res.json())
      .then(data =>
        this.setState({ totalflight: data.length, loading: false })
      );
  }

  componentWillUnmount() {
    this._isMounted = false;
    this.loading = true;
  }
  render() {
    const next = this.state.next;
    const latest = this.state.latest;
    const rockets = this.state.rockets;
    const roadster = this.state.roadster;
    const info = this.state.info;
    const totalflight = this.state.totalflight;

    return (
      <div>
        <div
          className="jumbotron"
          style={{ backgroundColor: "rgba(10,10,20,.0)" }}
        >
          <h2 className="text-center text-secondary">Welcome to</h2>
          <h3 className="text-center text-secondary">The SpaceX Data-Wiki</h3>
        </div>
        <>
          <div className="row">
            {info && totalflight && (
              <>
                <div className="col-sm-4">
                  <table className="table table-bordered">
                    <tbody>
                      <tr>
                        <th>founded</th>
                        <td>{info.founded}</td>
                      </tr>
                      <tr>
                        <th>founder</th>
                        <td>{info.founder}</td>
                      </tr>
                      <tr>
                        <th>CEO</th>
                        <td>{info.ceo}</td>
                      </tr>
                      <tr>
                        <th>COO</th>
                        <td>{info.coo}</td>
                      </tr>
                      <tr>
                        <th>CTO</th>
                        <td>{info.cto}</td>
                      </tr>
                      <tr>
                        <th>CTO propulsion</th>
                        <td>{info.cto_propulsion}</td>
                      </tr>
                      <tr>
                        <th>Employees</th>
                        <td>{info.employees}</td>
                      </tr>
                      <tr>
                        <th>CTO</th>
                        <td>{info.cto}</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                <div className="col-sm-4 d-flex align-items-center">
                  <div className="text-center ">
                    <h5 className="text-secondary">{info.summary}</h5>
                  </div>
                </div>
                <div className="col-sm-4">
                  <table className="table table-bordered">
                    <tbody>
                      <tr>
                        <th>Headquarters</th>
                        <td>
                          {info.headquarters.city} <br />{" "}
                          {info.headquarters.state}
                        </td>
                      </tr>
                      <tr>
                        <th>Launch sites</th>
                        <td>{info.launch_sites}</td>
                      </tr>
                      <tr>
                        <th>test sites</th>
                        <td>{info.test_sites}</td>
                      </tr>
                      <tr>
                        <th>Vehicles</th>
                        <td>{info.vehicles}</td>
                      </tr>
                    </tbody>
                  </table>
                  <table className="table table-bordered text-center">
                    <tbody>
                      <tr>
                        <th>
                          <h5>Total Flights</h5>
                        </th>
                      </tr>
                      <tr>
                        <th>
                          <h1>
                            {" "}
                            <CountUp end={totalflight} duration={8} />
                          </h1>
                        </th>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </>
            )}
          </div>
          <div className="row">
            <div className="col-md-6">
              {next && (
                <div className="card text-center">
                  <h4
                    className="text-primary"
                    style={{
                      backgroundColor: "#111",
                      margin: "0",
                      padding: "5px"
                    }}
                  >
                    Next launch
                  </h4>
                  <div
                    className="card-body"
                    style={{
                      backgroundColor: "#000",
                      display: "flex",
                      flexDirection: "column",
                      justifyContent: "center"
                    }}
                  >
                    <h2 className="text-success m-0">{next.mission_name}</h2>
                    <div className="p-5">
                      <h4>
                        <Countdown date={next.launch_date_local} />
                      </h4>
                      <h5 className="card-subtitle">
                        <Moment format="ll">{next.launch_date_local}</Moment>
                      </h5>
                    </div>
                    <Link
                      className="btn btn-secondary"
                      to={`/rockets/${next.rocket.rocket_id}`}
                    >
                      {next.rocket.rocket_name}
                    </Link>
                  </div>
                </div>
              )}
              {latest && (
                <div className="card text-center mt-5">
                  <h4
                    className="text-primary"
                    style={{
                      backgroundColor: "#111",
                      margin: "0",
                      padding: "5px"
                    }}
                  >
                    Latest launch
                  </h4>

                  <div
                    className="card-body"
                    style={{
                      backgroundColor: "#000",
                      display: "flex",
                      flexDirection: "column",
                      justifyContent: "center"
                    }}
                  >
                    <h2 className="text-success">{latest.mission_name}</h2>
                    <div className="card-body">{latest.details}</div>
                    {latest.links.flickr_images &&
                      latest.links.flickr_images.length > 1 && (
                        <div
                          className="d-flex flex-wrap justify-content-center"
                          style={{
                            alignItems: "center",
                            backgroundColor: "#000",
                            padding: "20px 0"
                          }}
                        >
                          {latest.links.flickr_images.map(image => {
                            return (
                              <div key={uuid()} style={{ width: "50%" }}>
                                <a
                                  href={image}
                                  target="_blank"
                                  rel="noopener noreferrer"
                                >
                                  <img
                                    src={image}
                                    alt="launch"
                                    style={{ width: "100%" }}
                                  />
                                </a>
                              </div>
                            );
                          })}
                        </div>
                      )}
                    <Link
                      className="btn btn-secondary"
                      to={`/launches/launch/${latest.flight_number}`}
                    >
                      Go to Launch
                    </Link>
                  </div>
                </div>
              )}
            </div>
            {rockets && (
              <div className="col-md-6">
                <div className="card text-center">
                  <h4
                    className="text-primary"
                    style={{
                      backgroundColor: "#111",
                      margin: "0",
                      padding: "5px"
                    }}
                  >
                    SpaceX Rockets
                  </h4>
                  <div
                    className="card-body"
                    style={{
                      backgroundColor: "#000",
                      display: "flex",
                      flexDirection: "column",
                      justifyContent: "center"
                    }}
                  >
                    {rockets.map(rocket => (
                      <div key={uuid()} className="mb-4">
                        <h3 className="text-warning text-center">
                          {rocket.rocket_name}
                        </h3>
                        <p className="text-secondary text-center">
                          Click on the image for more info
                        </p>
                        <Link to={`/rockets/${rocket.rocket_id}`}>
                          <img
                            src={rocket.flickr_images}
                            alt="launch"
                            style={{ width: "100%", paddingBottom: "10px" }}
                          />
                        </Link>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            )}
          </div>
          {roadster && (
            <div className="card text-center mt-5">
              <h4
                className="text-primary"
                style={{
                  backgroundColor: "#111",
                  margin: "0",
                  padding: "5px"
                }}
              >
                Where is roadster?!
              </h4>
              <div
                className="card-body"
                style={{
                  backgroundColor: "#000",
                  display: "flex",
                  flexDirection: "column",
                  justifyContent: "center"
                }}
              >
                <h6>{roadster.details}</h6>
                <div
                  className="card-body d-flex flex-wrap"
                  style={{
                    backgroundColor: "#000"
                  }}
                >
                  {roadster.flickr_images.map(img => (
                    <div key={uuid()} className="mb-4" style={{ width: "50%" }}>
                      <img
                        src={img}
                        alt="roadster"
                        style={{ width: "100%", paddingBottom: "10px" }}
                      />
                    </div>
                  ))}
                </div>
                <h4 className="text-center">
                  <Link to="/roadster">Click here to read the data</Link>
                </h4>
              </div>
            </div>
          )}
        </>
      </div>
    );
  }
}

export default Home;
