import React, { Component } from "react";
import Moment from "react-moment";
const uuid = require("uuid/v4");

class UpcomingLaunches extends Component {
  constructor(props) {
    super(props);
    this._isMounted = false;
    this.state = {
      launches: [],
      loading: true
    };
  }

  async componentDidMount() {
    this._isMounted = true;
    const url = "https://api.spacexdata.com/v3/launches";
    const res = await fetch(url);
    const data = await res.json();
    this._isMounted && this.setState({ launches: data, loading: false });
  }

  componentWillUnmount() {
    this._isMounted = false;
    this.loading = true;
  }

  render() {
    console.log(this.state.launches);

    return (
      <>
        <div className="d-flex justify-content-around">
          <h1 className="display-4 my-3">Upcoming Launches</h1>
        </div>

        {this.state.loading || !this.state.launches ? (
          <p>Loading...</p>
        ) : (
          <div
            className="d-flex flex-wrap"
            style={{ justifyContent: "center", textAlign: "center" }}
          >
            {this.state.launches.map(launch =>
              !launch.upcoming ? null : (
                <div
                  className="card m-2 mb-3"
                  style={{ width: "300px" }}
                  key={uuid()}
                >
                  <h4
                    style={{
                      backgroundColor: "#111",
                      margin: "0",
                      padding: "5px"
                    }}
                  >
                    <span className="text-primary">{launch.mission_name}</span>
                  </h4>
                  <div
                    className="card-body"
                    style={{
                      backgroundColor: "#000",
                      display: "flex",
                      flexDirection: "column",
                      justifyContent: "center"
                    }}
                  >
                    <p className="card-title">
                      <strong>Flight Number</strong>
                    </p>
                    <h2 className="text-success">{launch.flight_number}</h2>
                    <h5 className="card-subtitle">
                      <Moment format="ll">{launch.launch_date_local}</Moment>
                    </h5>
                  </div>
                  <table
                    className="table table-bordered text-center m-0"
                    style={{ backgroundColor: "#111" }}
                  >
                    <thead>
                      <tr>
                        <th>Site: </th>
                        <th>Rocket:</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>{launch.launch_site.site_name_long}</td>
                        <td>{launch.rocket.rocket_name}</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              )
            )}
          </div>
        )}
      </>
    );
  }
}

export default UpcomingLaunches;
