import React, { Component } from "react";
import { Link } from "react-router-dom";
import Moment from "react-moment";

const uuid = require("uuid/v4");

export default class Capsules extends Component {
  constructor(props) {
    super(props);
    this._isMounted = false;
    this.state = {
      capsules: [],
      loading: true
    };
  }

  componentDidMount() {
    this._isMounted = true;
    this._isMounted &&
      fetch("https://api.spacexdata.com/v3/capsules")
        .then(res => res.json())
        .then(data =>
          this.setState({
            capsules: data,
            loading: false
          })
        );
  }

  componentWillUnmount() {
    this._isMounted = false;
    this.loading = true;
  }
  render() {
    console.log(this.state.capsules);

    return (
      <div>
        <h2>Capsules</h2>

        {this.state.loading || !this.state.capsules ? (
          <p>Loading...</p>
        ) : (
          <>
            <table className="table table-bordered">
              <thead>
                <tr>
                  <th>ID</th>
                  <th>Type</th>
                  <th>Serial</th>
                  <th>Original Launch</th>
                  <th>Landings</th>
                  <th>Reused</th>
                  <th>Missions</th>
                  <th>Details</th>
                  <th>Status</th>
                </tr>
              </thead>
              <tbody>
                {this.state.capsules.map(capsule => {
                  return (
                    <tr key={uuid()}>
                      <td>{capsule.capsule_id}</td>
                      <td>{capsule.type}</td>
                      <td>{capsule.capsule_serial}</td>
                      <td>
                        <Moment format="ll">{capsule.original_launch}</Moment>
                      </td>
                      <td>{capsule.landings}</td>
                      <td>{capsule.reuse_count}</td>
                      <td>
                        {capsule.missions.map(m => {
                          return (
                            <div key={uuid()}>
                              <p>
                                {" "}
                                {m.name} <br />
                                <Link to={`/launches/launch/${m.flight}`}>
                                  Flight: {m.flight}
                                </Link>
                              </p>
                            </div>
                          );
                        })}
                      </td>
                      <td>{capsule.details}</td>
                      <td
                        className={
                          capsule.status === "active"
                            ? "text-success"
                            : capsule.status === "retired"
                            ? "text-warning"
                            : capsule.status === "destroyed"
                            ? "text-danger"
                            : null
                        }
                      >
                        {capsule.status}
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </>
        )}
      </div>
    );
  }
}
