import React, { Component } from "react";
import { Link } from "react-router-dom";
import Timeline from "./Timeline";
import Moment from "react-moment";
const uuid = require("uuid/v4");
const numeral = require("numeral");

class Launch extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      launch: null
    };
  }

  componentDidMount() {
    window.scrollTo(0, 0);
    let flight_number = this.props.match.params.flight_number;
    flight_number = parseInt(flight_number);
    const url = `https://api.spacexdata.com/v3/launches/${flight_number}`;

    fetch(url)
      .then(res => res.json())
      .then(launch => {
        this.setState({
          loading: false,
          launch: launch,
          video: `https://www.youtube.com/embed/${launch.links.youtube_id}`
        });
      });
  }

  render() {
    const launch = this.state.launch;
    console.log(launch);

    return (
      <>
        {launch && (
          <div>
            <h1 className="display-4 my-3">
              <span className="text-dark"></span> {launch.mission_name}
            </h1>
            <div className="row">
              <div
                className="video col-md-12"
                style={{
                  position: "relative",
                  paddingBottom: "56.25%" /* 16:9 */,
                  paddingTop: "25",
                  height: "item-fit"
                }}
              >
                <iframe
                  style={{
                    position: "absolute",
                    top: 0,
                    left: 0,
                    width: "100%",
                    height: "100%"
                  }}
                  title="spaceX"
                  src={this.state.video}
                  frameBorder="0"
                  allow="accelerometer; encrypted-media; picture-in-picture; fullscreen"
                ></iframe>
              </div>

              <div className="col-lg-6">
                <p className="m-0 p-2">{launch.details}</p>

                <h5
                  className="text-center text-secondary mt-3 mb-0 p-2"
                  style={{ border: "1px solid #282828" }}
                >
                  Launch details
                </h5>
                <table className="table table-bordered">
                  <tbody>
                    <tr>
                      <th>Flight#</th>
                      <td>{launch.flight_number}</td>
                    </tr>
                    <tr>
                      <th>Local date</th>
                      <td>
                        <Moment format="ll">{launch.launch_date_local}</Moment>
                      </td>
                    </tr>
                    <tr>
                      <th>Launch site</th>
                      <td>{launch.launch_site.site_name_long}</td>
                    </tr>
                    <tr>
                      <th>Success</th>
                      <td>
                        <p>
                          {launch.launch_success ? (
                            <span className="text-success">Yes</span>
                          ) : (
                            <span className="text-warning">No</span>
                          )}
                        </p>

                        {launch.launch_failure_details ? (
                          <p>
                            <strong>Reason:</strong>{" "}
                            {launch.launch_failure_details.reason}
                          </p>
                        ) : null}

                        {launch.launch_failure_details ? (
                          <p>
                            <strong>Time:</strong> +
                            {launch.launch_failure_details.time}s
                          </p>
                        ) : null}
                      </td>
                    </tr>
                  </tbody>
                </table>

                <Timeline timeline={launch.timeline} />
              </div>

              <div className="col-lg-6">
                <h5
                  className="text-center text-primary mt-3 mb-0 p-2"
                  style={{ border: "1px solid #282828" }}
                >
                  <Link to={`/rockets/${launch.rocket.rocket_id}`}>
                    {launch.rocket.rocket_name}
                  </Link>
                </h5>
                <table className="table table-bordered">
                  <tbody>
                    <tr>
                      <th>Type</th>
                      <td>{launch.rocket.rocket_type}</td>
                    </tr>
                    <tr>
                      <th className="text-primary">First Stage</th>
                      <td></td>
                    </tr>
                  </tbody>

                  {launch.rocket.first_stage.cores.map(core => {
                    return (
                      <tbody key={uuid()}>
                        <tr>
                          <th>Core serial</th>
                          <td className="text-success">{core.core_serial}</td>
                        </tr>
                        <tr>
                          <th>Block</th>
                          <td>{core.block}</td>
                        </tr>
                        <tr>
                          <th>Flight</th>
                          <td>{core.flight}x</td>
                        </tr>
                        <tr>
                          <th>Landing intent</th>
                          <td>{core.landing_intent ? "yes" : "no"}</td>
                        </tr>
                        {core.landing_intent && (
                          <tr>
                            <th>Landing success</th>
                            <td>{core.land_success ? "yes" : "no"}</td>
                          </tr>
                        )}
                        <tr>
                          <th>Landing type</th>
                          <td>
                            {core.landing_type ? core.landing_type : "n/a"}
                          </td>
                        </tr>

                        <tr>
                          <th>Landing vehicle</th>
                          <td>
                            {core.landing_vehicle
                              ? core.landing_vehicle
                              : "n/a"}
                          </td>
                        </tr>
                        <tr>
                          <th>Gridfins</th>
                          <td>{core.gridfins ? "yes" : "no"}</td>
                        </tr>
                        <tr>
                          <th>reused</th>
                          <td>{core.reused ? "Yes" : "no"}</td>
                        </tr>
                        <tr>
                          <th>Legs</th>
                          <td>{core.legs ? "Yes" : "no"}</td>
                        </tr>
                      </tbody>
                    );
                  })}
                  <tbody>
                    <tr>
                      <th className="text-primary">Second Stage</th>
                      <td></td>
                    </tr>
                    <tr>
                      <th>Block</th>
                      <td>{launch.rocket.second_stage.block}</td>
                    </tr>
                    <tr>
                      <th>
                        payload
                        {launch.rocket.second_stage.payloads.length > 1
                          ? "s"
                          : ""}
                        :
                      </th>
                      <td></td>
                    </tr>
                  </tbody>
                  {launch.rocket.second_stage.payloads.map(load => {
                    return (
                      <tbody key={uuid()}>
                        <tr>
                          <th>Customer</th>
                          <td>
                            {load.customers.map(cu => (
                              <p className="m-0" key={uuid()}>
                                {cu}
                              </p>
                            ))}
                          </td>
                        </tr>
                        <tr>
                          <th>Manufacturer</th>
                          <td>{load.manufacturer}</td>
                        </tr>
                        <tr>
                          <th>Manufacturer</th>
                          <td>{load.manufacturer}</td>
                        </tr>
                        <tr>
                          <th>Nationality</th>
                          <td>{load.nationality}</td>
                        </tr>
                        <tr>
                          <th>Orbit</th>
                          <td>{load.orbit}</td>
                        </tr>
                        <tr>
                          <th>Payload id</th>
                          <td>{load.payload_id}</td>
                        </tr>
                        <tr>
                          <th>Payload mass</th>
                          <td>
                            {numeral(load.payload_mass_kg).format(0, 0)} kg{" "}
                            <br />
                            {numeral(load.payload_mass_lbs).format(0, 0)} lbs
                          </td>
                        </tr>
                        <tr>
                          <th>Payload type</th>
                          <td>{load.payload_type}</td>
                        </tr>
                        <tr>
                          <th>Reused</th>
                          <td>{load.reused ? "yes" : "no"}</td>
                        </tr>
                      </tbody>
                    );
                  })}
                  {launch.rocket.fairings && (
                    <tbody>
                      <tr>
                        <th className="text-primary">Fairings</th>
                        <td></td>
                      </tr>
                      <tr>
                        <th>Reused</th>
                        <td>{launch.rocket.fairings.reused ? "yes" : "no"}</td>
                      </tr>
                      <tr>
                        <th>Recovery attempt</th>
                        <td>
                          {launch.rocket.fairings.recovery_attempt
                            ? "yes"
                            : "no"}
                        </td>
                      </tr>
                      {launch.rocket.fairings &&
                        launch.rocket.fairings.recovery_attempt && (
                          <tr>
                            <th>recovered</th>
                            <td>{launch.rocket.fairings ? "yes" : "no"}</td>
                          </tr>
                        )}
                    </tbody>
                  )}
                </table>
                <h5
                  className="text-center mt-3 mb-0 p-2"
                  style={{ border: "1px solid #282828" }}
                >
                  Links
                </h5>
                {launch.links.presskit && (
                  <h4 className="text-center p-2">
                    <a
                      href={launch.links.presskit}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      Presskit
                    </a>
                  </h4>
                )}
                {launch.links.reddit_campaign && (
                  <h4 className="text-center p-2">
                    <a
                      href={launch.links.reddit_campaign}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      Reddit Campaign
                    </a>
                  </h4>
                )}
                {launch.links.reddit_launch && (
                  <h4 className="text-center p-2">
                    <a
                      href={launch.links.reddit_launch}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      Reddit launch
                    </a>
                  </h4>
                )}

                {launch.links.reddit_media && (
                  <h4 className="text-center p-2">
                    <a
                      href={launch.links.reddit_media}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      Reddit media
                    </a>
                  </h4>
                )}

                {launch.links.reddit_recovery && (
                  <h4 className="text-center p-2">
                    <a
                      href={launch.links.reddit_recovery}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      Reddit recovery
                    </a>
                  </h4>
                )}

                {launch.links.wikipedia && (
                  <h4 className="text-center p-2">
                    <a
                      href={launch.links.wikipedia}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      Wikipedia
                    </a>
                  </h4>
                )}
              </div>
            </div>
            {launch.links.flickr_images &&
              launch.links.flickr_images.length > 1 && (
                <div
                  className="d-flex flex-wrap justify-content-center"
                  style={{
                    alignItems: "center",
                    backgroundColor: "#000",
                    padding: "20px 0"
                  }}
                >
                  {launch.links.flickr_images.map(image => {
                    return (
                      <div key={uuid()} style={{ width: "50%" }}>
                        <a
                          href={image}
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          <img
                            src={image}
                            alt="launch"
                            style={{ width: "100%" }}
                          />
                        </a>
                      </div>
                    );
                  })}
                </div>
              )}
          </div>
        )}

        <hr />
        <Link to="/launches">
          <h5 className="text-center p-3" style={{ backgroundColor: "#111" }}>
            back to launches
          </h5>
        </Link>
      </>
    );
  }
}

export default Launch;
